import Vue from 'vue'
import Default from './layouts/default.vue'

Vue.config.productionTip = false
//路由
import router from './plugins/router.js'
import "./assets/css/base.css"
import "./assets/font/font.js"

import axios from 'axios';

//引入拦截器的配置
import './plugins/axios.js'

import Router from 'vue-router'
 
// 解决重复点击导航路由报错
const originalPush = Router.prototype.push;
Router.prototype.push = function push(location) {
  return originalPush.call(this, location).catch(err => err);
}



//引入状态管理实例
import store from './plugins/vuex.js'
import * as types from './store/types.js'
Vue.prototype.$types=types;//为了在组件内部使用types类型，无需处处导入

// store.dispatch('detail/'+types.UPDATE_DETAIL)
let pailist = window.localStorage.getItem('pailist');
if(pailist){
	// console.log(JSON.parse((pailist)).id)
	
	// let id =JSON.parse((pailist)).id
	let id =62
	let collectionName = JSON.parse((pailist)).collectionName
		store.dispatch('pailist/'+types.UPDATE_PAILIST,{collectionName,id})
}


let seclist = window.localStorage.getItem('seclist');
if(seclist){
	// console.log(JSON.parse((pailist)).id)
	
	let w =JSON.parse((seclist)).w
	let collectionName = JSON.parse((seclist)).collectionName
		store.dispatch('seclist/'+types.UPDATE_SECLIST,{collectionName,w})
}

// Vue.prototype.$axios=axios

let vm = new Vue({
    router,
	 store,
    render: h => h(Default),
}).$mount('#app')

export default vm;