import Vue from "vue"
import Vuex from "vuex"


Vue.use(Vuex)

import actions from "../store/actions.js"
import mutations from '../store/mutations.js'
import getters from '../store/getters.js'
import state from '../store/state.js'

import detail from '../store/modules/detail.js'
import paihang from '../store/modules/paihang.js'
import pailist from '../store/modules/pailist.js'
import seclist from '../store/modules/seclist.js'
import ting from '../assets/ting.js'
import serch from '../store/modules/serch.js'

let store = new Vuex.Store({
	actions,mutations,getters,state,
	modules:{
		detail,paihang,pailist,seclist,ting,serch
	}
})

export default store;