import * as types from '../types.js';
import axios from '../../plugins/axios.js';
const state={
 reshou:[]
}

const actions = {

	  [types.UPDATE_SERCH]: ({state,commit},payload)=>{
		axios({
          url:"/hotSearch",
		}).then( 
		  (res)=>{
			  commit(types.UPDATE_SERCH,res.data.data.hotkey)  
		  }
		)
	}
}

const mutations = {
	
	[types.UPDATE_SERCH]:(state,payload)=>{state.reshou=payload},
	
}

const getters = {
  
}

export default {
  namespaced:true,
  state,actions,mutations,getters
}