import Vue from 'vue';
import VueRouter from 'vue-router';
Vue.use(VueRouter);

// import Home from '../pages/home.vue';
import PaiHang from '../pages/paihang.vue';
// import User from '../pages/user.vue';
import PaiList from '../pages/pailist.vue';
import Ting from '../pages/ting.vue';
import seclist from '../pages/seclist.vue';
import Detail from '../pages/detail.vue';
import Serch from "../pages/serch.vue"
// import NoPage from '../pages/no-page.vue';


let routes = [
    // { path: '/home', component: Home },
    
    { path: '/paihang', component: PaiHang,name:"PaiNav1" },
    { path: '/pailist', component: PaiList,name:"PaiList" },
    { path: '/ting', component: Ting,name:"Ting" },
	{ path: '/serch', component: Serch,name:"Serch" },
	{ path: '/seclist', component: seclist ,name:"seclist"},
    { path: '/detail', component: Detail, name: 'detail' },
    { path: '/', redirect: '/detail' },
    // { path: '*', component: NoPage }
	
];


let router = new VueRouter({
    routes,
    mode:'hash',
    scrollBehavior (to, from, savedPosition) {
        return { x: 0, y: 0 }
    }
})

export default router;