import * as types from '../types.js';
import axios from '../../plugins/axios.js';
const state={
 banner:[],
 banner2:[],
 banner3:[],

}

const actions = {
	[types.UPDATE_DETAIL]:({state,commit},payload)=>{
		
		axios({
		  url:'/recommend',
		}).then(
		  //res=>this.banner=res.data.homeData.officialPlaylist 
		  (res)=>{	
			  // commit(types.UPDATE_DETAIL,res.data.homeData.officialPlaylist)
			  commit(types.UPDATE_DETAIL,{
				  banner:res.data.homeData.officialPlaylist,
				  banner2:res.data.homeData.ugcPlaylist,
				  banner3:res.data.homeData.zoneList  
				  })
			  
			  // this.banner=res.data.homeData.officialPlaylist 
			  // this.banner2=res.data.homeData.ugcPlaylist
			   // this.banner3=res.data.homeData.zoneList
		  }
		)
	},

}

const mutations = {
	[types.UPDATE_DETAIL]:(state,payload)=>{ 
		state.banner=payload.banner
		state.banner2=payload.banner2
		state.banner3=payload.banner3
		},
	
	
}

const getters = {
  
}

export default {
  namespaced:true,
  state,actions,mutations,getters
}