<template>
   <div class="banner">
       
       		<div class="swiper-container clearfix" >
       		    <div class="swiper-wrapper">
       		        <router-link
					v-for="item of data"
					:key="item._id"
					:to="{name:'detail',params:{_id:item.id},query:{collectionName:collectionName}}"
					tag="div"
					:style="{width:width}"
					class="swiper-slide">
					<span class="ding">{{(item.cnt)/1000}}万</span>
					<img :src='item.cover' alt=""
						:style="{width:width,height:height}"
					>
					{{item.title}}
					</router-link>
					
       		       
       		    </div>
       		    <!-- 如果需要分页器 -->
       		    <div class="swiper-pagination"></div>
       		</div>    
       		    <!-- 如果需要导航按钮 -->
       		    <!-- <div class="swiper-button-prev"></div> -->
       		    <!-- <div class="swiper-button-next"></div> -->
       		    
       		    <!-- 如果需要滚动条 -->
       		    <!-- <div class="swiper-scrollbar"></div> -->
  </div>
  
  
</template>

<script>
	import "./swiper.min.css"
	import  Swiper from  './swiper.min.js';
  // console.log('swiper',Swipe)
  import $ from 'jquery';
  export default {
    name:'banner',
    //props:['data','collectionName',],
	props:{
		data:{
			type:Array ,
			 default:{}
		},
		collectionName:{
			type:String,
			 default:''
		},
		width:{
			type:String,
			 default:'109px',
		},
		height:{
			type:String,
			 default:'109px',
		},
	},
    components:{},
    mounted(){
      this.initSwiper()
     //this.data.length > 0 && this.initSwiper()
	  //console.log('mounted',this.$router.to);
    },
    updated(){
      // console.log('updated')
     this.initSwiper()
    },
    methods:{
      initSwiper(){
	  var mySwiper = new Swiper ('.swiper-container', {
			    direction: 'horizontal', // 垂直切换选项
			    loop: false, // 循环模式选项
				freeMode : true,
				freeModeMomentumBounce : false,
				slidesPerView: 'auto',
				loopedSlides: 2,
				touchRatio : 0.5,//触摸变慢
			    
			    // 如果需要分页器
			    pagination: {
			      // el: '.swiper-pagination',
			    },	    
			  })        
		
	}
  }
}
</script>

<style scoped>

.swiper-slide{font-size: 12px;color: rgba(26,26,26,1);font-size: .12rem;font: 300 12px/1.5 -apple-system,sans-serif;}
.swiper-slide img{
	width: 109px;height: 109px;border-radius: 5%;
}
.ding{position: absolute; right: 10px;bottom: 40px;display:block;background:gray;opacity: 0.5;border-radius: 10%;}
	.swiper-container {
			   width: 100%;
			    height: auto;
				/* background-color: red */
			}  
			.swiper-slide {
				/* width: 109px; */
				height: auto;
				margin-right: 20px;
			
			}
		
		
</style>
