import * as types from '../types.js';
import axios from '../../plugins/axios.js';
const state={
		pai1:[],
		pai2:[],
		pai3:[],
		pai4:[]
     }

const actions = {
	
	[types.UPDATE_PAIHANG]:({state,commit},payload)=>{
		axios({
			url:'/tolist',
		}).then( 
		  (res)=>{
			  commit(types.UPDATE_PAIHANG,{			
				pai1:res.data.topListData.list[0].toplist,
				pai2:res.data.topListData.list[1].toplist,
				pai3:res.data.topListData.list[2].toplist,
				pai4:res.data.topListData.list[3].toplist})  
		  }
		)
	}
	
}

const mutations = {
	
	[types.UPDATE_PAIHANG]:(state,payload)=>{ 
		state.pai1=payload.pai1
		state.pai2=payload.pai2
		state.pai3=payload.pai3
		state.pai4=payload.pai4
		
		},
	
}

const getters = {
  
}

export default {
  namespaced:true,
  state,actions,mutations,getters
}