import * as types from '../types.js';
import axios from '../../plugins/axios.js';
const state={
	uc:[]
}

const actions = {

	  [types.UPDATE_SECLIST]: ({state,commit},{collectionName,w})=>{
		axios({
		url:`/${collectionName}/?w=${w}?n=10`,
		}).then( 
		  (res)=>{
			  commit(types.UPDATE_SECLIST,res.data.data.song.list)  
		  }
		)
	}
}

const mutations = {
	
	[types.UPDATE_SECLIST]:(state,payload)=>{
	
		state.uc=payload
		
		},
	
}

const getters = {
  
}

export default {
  namespaced:true,
  state,actions,mutations,getters
}