import * as types from '../types.js';
import axios from '../../plugins/axios.js';
const state={
  list:[],
  list1:[],
  UcCell:[]
}

const actions = {

	  [types.UPDATE_PAILIST]: ({state,commit},{collectionName,id})=>{
		axios({
		 url:`/${collectionName}/?id=${id}`,
		}).then( 
		  (res)=>{
			  commit(types.UPDATE_PAILIST,{
				  UcCell:res.data.songlist,
				  list:res.data,
				  list1:res.data.topinfo,
				  })  
		  }
		)
	}
}

const mutations = {
	
	[types.UPDATE_PAILIST]:(state,payload)=>{
		state.UcCell=payload.UcCell
		state.list=payload.list
		state.list1=payload.list1
		
		
		},
	
}

const getters = {
  
}

export default {
  namespaced:true,
  state,actions,mutations,getters
}