<template>
  <div class="uc-cell" @click="getDetail()">
    <div @click="getDetail">
      <h2><span class="s1" v-show="index!==null">{{index + 1}}.</span><span class="s2">{{datas.albumname}}</span>
	  </h2>
	  <b>⚘</b>
      <p>{{datas.singer[0].name}}</p>
    </div>
    <a v-if="isAdd" href="javascript:;" @click="$emit('click',datas)">+</a>
  </div>
</template>

<script>
  export default {
    name:'uc-cell',
    props:{
      datas:{
        type:Object,
        required:true
      },
      index:{
        type:Number,
        default:null
      },
      url:{
        type:String,
        default:''
      },
      to:{
        type: Object,
        default:null
      },
      isAdd:{
        type: Boolean,
        default:false
      }
    },
    
    components:{},
    mounted(){},
    updated(){
		// console.log("data",data)
	},
    methods:{
      getDetail(){
        if(this.url){
          window.open(this.url,'_blank');
        }else if(this.to){
			
          this.$router.push({name:this.to.name,params:this.to.params,query:this.to.query})
		   console.log({name:this.to.name,params:this.to.params,query:this.to.query})
        }
      }
    }
  }
</script>

<style scoped>
  .uc-cell{height: 60px;padding: 0 10px;padding-top: 10px;position: relative;}
  .s1{font-size: 12px;color: #666666;margin: 11px;}
  .s2{font-weight: 500;font-size: 14px}
  b{position: absolute;right: 57px;bottom: 25px;}
  p{padding-left: 33px;}
</style>
